.editable-table {
  width: 100%;
  //border-collapse: separate;
  //border-spacing: 10px;
  border: 1px solid #F2F2F2;
  border-radius: 5px;
  margin-top: 1rem;
  tr {
    padding: 0 1rem;
    th {
      box-shadow: 0px 6px 12px #3d4b5c1a;
      border-radius: 5px;
      padding: 0.5rem 1rem;
      text-align: left;
      background: #FFFFFF;
      &.short {
        width: 200px;
      }
    }
    td {
      padding: 0.5rem 1rem;
      text-align: left;
      border-top: 1px solid #F2F2F2;
    }
  }
}